<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">请假审批</p>
        </div>
        <div class="content" :style="{paddingTop:'0.84rem',paddingBottom:active?'1.7rem':'1rem'}">
            <table class="f28">
                <tbody>
                    <tr>
                        <td>申请人：</td>
                        <td>{{data.studentName}}</td>
                    </tr>
                    <tr>
                        <td>申请时间：</td>
                        <td>{{data.createTime | fileTime(data.createTime)}}</td>
                    </tr>
                    <tr>
                        <td>请假时间：</td>
                        <td>{{data.startTime.replace(/-/g,'.').substring(0,16)}}-{{data.endTime.replace(/-/g,'.').substring(0,16)}}</td>
                    </tr>
                    <tr>
                        <td>请假理由：</td>
                        <td>{{data.content}}</td>
                    </tr>
                </tbody>
            </table>
            <div class="pl-30 pr-30 pb-60 b_line">
                <img class="mb-10" v-for="(item,index) in data.imageUrls" :key="index" :src="$api.fileUrl + item" alt="" >
            </div>
            <div class="sendOut f28 c333 b_line pl-30" v-if="showBtn">
                <span>推送至</span>
            </div>
            <div class="sendOut f24 c333 pl-30 pr-30" v-else>
                <span>推送至</span>
                <span>{{sendArr.toString()}}</span>
            </div>
            <div>
                <ul class="f24 pl-30">
                    <li class="b_line pr-30" v-for="(item,index) in data.leaveInfo" :key="index" v-if="showBtn">
                        <p>{{item.name}}</p>
                        <button class="bg_6b7" v-if="item.state == 5" @click="insertLeaveRole(item.id,item.name)">发送</button>
                        <p class="c999" v-else-if="item.state == 1">已通过</p>
                        <p class="c999" v-else-if="item.state == 2">未审核</p>
                        <p class="c999" v-else-if="item.state == 3">已失效</p>
                        <p class="c999" v-else-if="item.state == 4">待审核</p>
                    </li>
                </ul>
                <!-- <ul class="f24 pl-30" v-else>
                    <li class="b_line pr-30">
                        <p>校长</p>
                        <p class="c999">待审核</p>
                    </li>
                    <li class="b_line pr-30">
                        <p>副校长</p>
                        <p class="c999">待审核</p>
                    </li>
                    <li class="b_line pr-30">
                        <p>教务处</p>
                        <p class="c999">未通过</p>
                    </li>
                    <li class="b_line pr-30">
                        <p>政教处</p>
                        <p class="c999">已通过</p>
                    </li>
                </ul> -->
            </div>
        </div>
        <div class="centerBtn f26 cfff text-center" v-if="active && data.teacherState == 4" @click="agree">审 核</div>
        <div class="centerBtn f26 cfff text-center" v-else-if="active && (data.teacherState == 2 || data.teacherState == 3)" @click="agree">已审核</div>
        <div class="centerBtn f26 cfff text-center" v-else @click="agree">待确认</div>
       <!-- 弹窗 -->
        <van-popup v-model="show" class="f30 c999">
            <div class="question">
                <p class="c333">提示</p>
                <p class="f28 mt-70">是否同意请假信息？</p>
            </div>
            <div class="choose pt-30 pb-30">
                <p @click="cancel">不同意</p>
                <p class="c333" @click="confirm">同意</p>
            </div>
        </van-popup>
    </div>
</template>

<script>
import {publisFn} from '../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            active:false,//是否审批
            approval:true,//点击弹窗确定按钮
            show:false,
            data:{
                studentName:'',
                content:'',
                createTime:'',
                endTime:'',
                imageUrls:[],
                startTime:'',
                fuxiaozhang:'',
                jiaowuchu:'',
                leaveDay:'',
                xiaozhang:'',
                leaveInfo:[],
                teacherState:0
            },
            showBtn:false,
            sendPerson:'家长、教务处、政务处',
            sendArr :[]
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num)
        }
    },
    //created() {
   //     this.active = this.$route.query.active == 'true' ? true : false;
    //},
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        turnToMy(){
            this.$router.push({path:'/my'})
        },
        turnToIndex(){
            this.$router.push({path:'/indexs'})
        },
        agree(){
            this.show=true;
        },
        cancel(){//不同意
            var that = this;
            var param = {
                roleId:JSON.parse(localStorage.getItem('zhxy_user')).id,
                leaveId:this.$route.query.id,
                state:2,//1.审核通过，2.审核失败 3.已失效4.审核中
                type:2,//	1.班主任 2.学校角色 3家长
            };
            for(var i in this.data.teacherRole){
                if(this.data.teacherRole[i] == '班主任'){
                    param.type = 1;
                    break
                }else{
                    for(var j in this.data.leaveInfo){
                        if(this.data.leaveInfo[j].name == this.data.teacherRole[i]){
                            param.roleId = this.data.teacherRole[j].id;
                        }
                    }
                }
            }
            this.$http({url:this.$api.message.saveOrUpdateLeaveRole},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$toast('审核成功');
                }else{
                    this.$toast('审核失败');
                }
                this.getLeaveInfo();
                this.show=false;
                this.$router.push({path:'/leaveDetail',query:{active:false,id:that.$route.query.id,type:1}})
            },err => {
                this.$toast('出错了');
            })
            
        },
        confirm(){//同意
            var that = this;
            var param = {
                roleId:JSON.parse(localStorage.getItem('zhxy_user')).id,
                leaveId:this.$route.query.id,
                state:1,//1.审核通过，2.审核失败 3.已失效4.审核中
                type:1,//	1.班主任 2.学校角色 3家长
            };
            for(var i in this.data.teacherRole){
                if(this.data.teacherRole[i] == '班主任'){
                    param.type = 1;
                    break
                }else{
                    for(var j in this.data.leaveInfo){
                        if(this.data.leaveInfo[j].name == this.data.teacherRole[i]){
                            param.roleId = this.data.leaveInfo[j].id;
                        }
                    }
                    
                }
            }
            this.$http({url:this.$api.message.saveOrUpdateLeaveRole},param).then(data => {
                console.log(data);
                // debugger
                if(data.code == 0){
                    this.$toast('审核成功');
                }else{
                    this.$toast('审核失败');
                }
                this.getLeaveInfo();
                this.show=false;
                this.$router.push({path:'/leaveDetail',query:{active:false,id:that.$route.query.id,type:1}})
            },err => {
                this.$toast('出错了');
            })

            // this.approval=false;
            // this.show=false;
        },
        insertLeaveRole(id,name){//推送给校长，副校长，政教处，家长
            var param = {
                leaveId : this.$route.query.id,
                roleId:id,
                state:4,
                type:1
            };
            if(name == '校长' || name == '副校长'  || name == '政教处' || name == '教务处'){
                param.type = 2;
            }else{
                param.type = 3;
            }
            this.$http({url:this.$api.message.insertLeaveRole},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.getLeaveInfo();
                }
            })
        },
        getLeaveInfo(){//获取详情
            this.$toast.loading({
                mask: true,
                duration:0
            });
            var that = this;
            this.$http({url:this.$api.message.getLeaveInfo},{leaveId:this.$route.query.id,schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId,teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    var leaveInfo = data.data.leaveInfo;
                    var leaveInfoLen = data.data.leaveInfo.length;
                    var teacherRole = data.data.teacherRole;
                    var teacherRoleLen = data.data.teacherRole.length;
                    if(that.$route.query.type==2){
                        for(var j = 0; j < teacherRoleLen;j++){
                            if(teacherRole[j] == '班主任'){
                               this.showBtn = true;
                               break
                            }
                        }
                        for(var i in leaveInfo){
                            for(var j in teacherRole){
                                if(leaveInfo[i].name == teacherRole[j]){
                                    leaveInfo.splice(i,1);
                                }
                            }
                        }
                        // for(var i in leaveInfo){
                        //      this.sendArr.push(leaveInfo[i].name)
                        // }                      
                    }else{
                        for(var i in leaveInfo){
                            if(leaveInfo[i].state != 5 && leaveInfo[i].state != 2){
                                this.sendArr.push(leaveInfo[i].name)
                            }
                        }
                    }
                    this.data = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data,msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }
    },
    created(){
        this.active = this.$route.query.type == '1' ? true : false;
        this.getLeaveInfo();
        
    } 
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
    table{
        padding:0.3rem;
        display: block;
        line-height: .9rem;
    }
    tr td:first-child{
        color:#666;
        width:1.5rem;
    }
    tr td:last-child{
        color:#333;
    }
    .content img{
        width:1.2rem;
        height:1.2rem;
        margin-right:0.08rem;
    }
    li{
        display: flex;
        justify-content: space-between;
        height:.9rem;
        line-height: .9rem;
        align-items: center;
    }
    li button{
        width:.7rem;
        height:.34rem;
        border:0;
        line-height: .34rem;
        color:#fff;
        border-radius: 0.05rem;
        padding: 0;
    }
    .sendOut{
        line-height: .9rem;
        display: flex;
        justify-content:space-between;
    }
    .van-popup{
        width:6rem;
        text-align: center;
        border-radius:0.16rem;
    }
    .question{
        padding:0.39rem 0 .7rem;
    }
    .choose{
        border-top:1px solid #EBEBEB;
        display: flex;
    }
    .choose p{
        width: 50%;
    }
    .choose p:first-child{
        border-right:1px solid #EBEBEB;
    }
</style>




